// $screen-sm: 576px
// $screen-md: 768px +
// $screen-lg: 992px +
// $screen-xl: 1200px +

@media screen and (max-width: 1007px) {
	main {
		section.mainImgContainer {
			width: auto; }
		section.description {
			width: auto;
			height: auto; }
		section.works-separator {
			display: flex;
			img {
				width: 20%; } }
		section.works {
			grid-template-columns: repeat(3, 220px);
			.works-container {
				.works-bottom {
					margin-bottom: 10px; } } } }
	main.about {
		section.aboutContainerStaff {
			grid-template-columns: repeat(2,300px); } } }
@media screen and (max-width: 1003px) {
	main.projects,main.blog,main.contact {
		flex-direction: column; } }
@media screen and (max-width: 998px) {
	main.about {
		section.aboutContainer {
			grid-template-columns: repeat(2,275px); } } }
@media screen and (max-width: 976px) {
	main {
		section.projects {
			grid-template-columns: repeat(2, 275px);
			.projects-bottom {
				margin-bottom: 10px; } } } }

@media screen and (max-width: 981px) {
	header {
		section.headerContainer {
			nav {
				.topnav {
					margin: 0 0 0 50px; } } } } }
//responsive menu
@media screen and (max-width: 900px) {
	header {
		section.headerContainer {
			width: 100%;
			nav {
				width: 100%;
				.topnav {
					.menuGroup {
						display: none;
						margin-left: auto;
						&.icon {
							display: flex; } } }
				.topnav.responsive {
					flex-direction: column;
					height: 450px;
					position: relative;
					top: 60px;
					left: -200px;
					.menuGroup {
						display: flex;
						margin-bottom: -30px;
						margin-left: 0;
						&.icon {
							position: absolute;
							top: -60px;
							right: -200px; }
						&:hover .triangleDown {
							visibility: hidden; } } } } } } }
@media screen and (max-width: 768px) {
	main {
		section.works {
			grid-template-columns: repeat(2, 220px); } } }
@media screen and(max-width:728px) {
	main.projects {
		section.leftBlock {
			.posts {
				grid-template-columns: repeat(2, 209px); } } } }
@media screen and (max-width: 688px) {
	main.about {
		section.aboutContainerStaff {
			grid-template-columns: repeat(1,300px); } } }
@media screen and (max-width: 679px) {
	main.about {
		section.aboutContainer {
			grid-template-columns: repeat(1,300px); } } }
@media screen and (max-width: 657px) {
	main {
		section.projects {
			grid-template-columns: repeat(1, 220px); } } }
@media screen and (max-width: 560px) {
	main.contact {
		section.leftBlock {
			.contactForm {
				p {
					textarea {
						width: 216px; } } } } } }
@media screen and (max-width: 528px) {
	main {
		section.mainImgContainer {
			width: auto;
			.left-block {
				width: auto;
				height: auto;
				// padding: 0
				padding: 0px 17px;
				p {
					&:first-child {
						font-size: 17px; }
					&:last-child {
						font-size: 20px; } } } }
		section.works {
			grid-template-columns: repeat(1, 220px); } } }
@media screen and (max-width: 503px) {
	main.projects {
		section.leftBlock {
			.posts {
				grid-template-columns: repeat(1, 209px); } } } }
@media screen and (max-width: 445px) {
	main.projects,main.blog,main.contact {
		section.leftBlock {
			section.worldsContainer {
				span {
					display: block;
					text-align: center; } } } } }
